import React, { Component } from 'react';

class FairytaleQuestion extends Component {
    state = { 
        //Story fairytale:
        questions: [],
        qid: 0,
        suggestion: '',
        submision: '',
        submitToParent: null,
    }
    constructor(props) {
        super(props);
        //Do it something!
        console.log('Constructor got: ' + props)
        console.log('Constructor got qid: ' + props.qid)
        //this.setState({submision: suggestion})
        this.state.qid = props.qid
        this.state.submitToParent = props.cb
        this.state.questions = props.q
        console.log('Constructor got callback: ' + props.cb)
    }
    generateResponse = () => {
        var qid = this.state.qid
        var question = this.state.questions[qid].title
        var suggestions = this.state.questions[qid].suggestions
        var rnd_idx = Math.floor(Math.random() * suggestions.length) ;
        var suggestion = suggestions[rnd_idx];
        this.setState({submision: suggestion});
        console.log(question + ' Suggested: ' + suggestion);
        console.log(qid)
        this.state.submitToParent(
            {
                submission: suggestion,
                qid: qid
            }
        )
    }
    submissionChange = (e) => {
        this.setState({submision: e.target.value});
        console.log('Submission done: ' + e.target.value);
        this.state.submitToParent(
            {
                submission: e.target.value,
                qid: this.state.qid
            }
        )
    }
    render() { 
        return ( 
            <div className="col-sm">
            <div className="alert alert-light" role="alert">
                <h5 className="alert-heading">{this.state.questions[this.state.qid].title}</h5>
                <hr/>
                <div className="btn-group-vertical">
                <input value={this.state.submision} onChange={this.submissionChange} type="text" className="form-control" placeholder="Location" aria-label="Recipient's username" aria-describedby="button-addon2"/>
                <button onClick={this.generateResponse} type="button" className="btn btn-outline-primary btn-long">Generate</button>
                </div>
            </div>
            </div>
        );
    }
}
 
export default FairytaleQuestion;