    console.log(qid)
    //Submit the question choices in the module state:
    var updated_questions = this.state.questions
    updated_questions[qid].choices = submission.slice(0)
    console.log('Updated Submission: ', updated_questions[qid].choices)
    this.setState({questions: updated_questions}, () => {
      console.log('Updated the submission: '+updated_questions[qid].choices)
    })
